.task {
    position: relative;
    display: flex;
    align-items: center;
    width: 100%;
    height: 4.5rem;
    border: 0.0625rem solid rgb(218, 220, 224);
    border-radius: 1rem;
}

.taskContainer {
    display: flex;
}

.taskContent {
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.taskText {
    display: inline-block;
    text-overflow: ellipsis ellipsis;
    overflow: hidden;
    white-space: nowrap;
    letter-spacing: .01785714em;
    font-family: Roboto, Arial, sans-serif;
    font-size: 0.875rem;
    font-weight: 500;
    line-height: 1.25rem;
    width: 40vw;
}

.taskDate {
    letter-spacing: .025em;
    font-family: Roboto, Arial, sans-serif;
    font-size: 0.75rem;
    font-weight: 400;
    line-height: 1rem;
    color: #5f6368;
}

.taskLink {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 2;
}


.distance {
    width: 10vw;
}

@media (max-width: 1040px) {
    .distance {
        width: 10vw;
    }
}
.tripleDotsIcon {
    cursor: pointer;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
}

.tripleDotsContainer {
    display: flex;
    align-items: center;
    width: 100%;
    justify-content: flex-end;
}
